.appShell {
  min-height: 100vh;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: #000;
  color: inherit;
  overflow: hidden;
}

.appHeader {
  display: flex;
  flex-direction: column;
  width: 100%;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 10;
  background: #000;
}

.pages {
  flex: 1;
  min-height: 0;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  overflow-x: hidden;
  
}

.tasksRoute {
  flex: 1;
  min-height: 0;
  display: flex;
  height: 100%;
}
