.navbar {
  background-color: #326b7e;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  width: 100%;
  position: relative;
}

.nav-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80px;
  max-width: 1500px;
}

.nav-logo {
  color: #f5b921;
  align-items: center;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  flex-grow: 1;
  font-family: 'Lora', serif;
  font-size: 2em;
  font-weight: 700;
}

.nav-menu {
  display: flex;
  list-style: none;
  text-align: center;
  margin-right: 2rem;
}

.nav-links {
  color: #fff;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  border-bottom: 3px solid transparent;
  font-family: 'Lora', serif;
}

.fa-code {
  margin-left: 1rem;
}

.nav-item {
  line-height: 40px;

}

.nav-item:after {
  content: "";
  display: block;
  height: 3px;
  width: 0;
  background: transparent;
  transition: width 0.7s ease, background-color 0.5s ease;
}

.nav-item:hover:after {
  width: 100%;
  background: #FFA400;
}

.nav-item .active {
  color: #FFA400;
  border: 1px solid #FFA400;
}

.nav-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .nav-menu {
    display: none;
    display: flex;
    flex-direction: column;
    width: fit-content;
    border-top: 1pxsolid #fff;
    position: absolute;
    top: 60px;
    left: -110%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #1f5156;
    left: 0px;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    width: fit-content;
    padding-left: 0;
    top: 60px;
    margin: 0 auto;
  }

  .nav-item .active {
    color: #FFA400;
    border: none;
  }

  .nav-links {
    padding: 1.5rem;
    width: 100%;
    display: table;
  }

  .nav-icon {
   
    height: 90%;
    display: block;
    position: relative;
    top: 0;
    right: -1;
    font-size: 1.8rem;
    cursor: pointer;
    color: #FFA400;
  }

  .nav-icon img {
    margin: 0;
    height: 100%;
    width: auto;
  }
}