:root {
  --vh: 1vh;
  --safe-top: 0px;
  --safe-bottom: 0px;
  --safe-left: 0px;
  --safe-right: 0px;
  --todo-bg: rgba(9, 13, 30, 0.95);
  --panel-bg: #050917;
  --panel-border: #1e253c;
  --panel-border-strong: #5ec7f4;
  --text-primary: #f4f6fb;
  --text-muted: #94a0bf;
  --accent: #5ec7f4;
  --danger: #ff6b6b;
  --card-radius: 16px;
}

@supports (padding: env(safe-area-inset-top)) {
  :root {
    --safe-top: env(safe-area-inset-top);
    --safe-bottom: env(safe-area-inset-bottom);
    --safe-left: env(safe-area-inset-left);
    --safe-right: env(safe-area-inset-right);
  }
}

#section-create {
  padding: 0%;
}

.todoViewport {
  flex: 1;
  display: flex;
  width: 100%;
  height: 100%;
  min-height: 0;
  justify-content: center;
  padding: calc(0.2rem + var(--safe-top)) calc(0.2rem + var(--safe-right)) calc(0.2rem + var(--safe-bottom)) calc(0.2rem + var(--safe-left));
  box-sizing: border-box;
  overflow: hidden;
}

.todoApp {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  color: var(--text-primary);
  padding: calc(1rem + var(--safe-top)) calc(1.25rem + var(--safe-right)) calc(1rem + var(--safe-bottom)) calc(1.25rem + var(--safe-left));
  margin: 0;
  min-height: 0;
  box-sizing: border-box;
  overflow: hidden;
  background: var(--todo-bg);
  border: 1px solid rgba(255, 255, 255, 0.06);
  border-radius: var(--card-radius);
}

.todoApp>* {
  min-width: 0;
}

.todoAppHeader {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  align-items: flex-start;
  flex-wrap: wrap;
  flex-shrink: 0;
}

.todoAppHeader h2 {
  margin: 0.25rem 0;
}

.eyebrow {
  text-transform: uppercase;
  letter-spacing: 0.2em;
  font-size: 0.75rem;
  color: var(--text-muted);
  margin: 0;
}

.subtitle {
  margin: 0;
  color: var(--text-muted);
  max-width: 640px;
}

.statsPill {
  border: 1px solid var(--panel-border);
  border-radius: 999px;
  padding: 0.75rem 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: 140px;
}

.statsPill span {
  font-size: 1.85rem;
  font-weight: 700;
}

.statsPill small {
  color: var(--text-muted);
}

.sectionNavBar {
  background: rgba(5, 9, 23, 0.9);
  border: 1px solid var(--panel-border);
  border-radius: var(--card-radius);
  padding: 0.5rem;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.04);
  position: -webkit-sticky;
  position: sticky;
  top: calc(var(--safe-top) + 0.75rem);
  z-index: 5;
  -webkit-backdrop-filter: blur(12px);
  backdrop-filter: blur(12px);
  flex-shrink: 0;
}

.sectionNav {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 0.5rem;
}

.sectionNavButton {
  border-radius: var(--card-radius);
  border: 1px solid var(--panel-border);
  background: rgba(255, 255, 255, 0.02);
  color: var(--text-primary);
  padding: 0.85rem 1rem;
  text-align: left;
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
  font-size: 0.95rem;
  cursor: pointer;
  transition: border-color 0.2s ease, background 0.2s ease;
}

.sectionNavButton small {
  color: var(--text-muted);
  font-size: 0.75rem;
}

.sectionNavButton.is-active {
  border-color: var(--panel-border-strong);
  background: rgba(94, 199, 244, 0.12);
}

.sectionNavButton:not(:disabled):hover,
.sectionNavButton:not(:disabled):focus-visible {
  border-color: var(--panel-border-strong);
  background: rgba(94, 199, 244, 0.18);
}

.sectionsGrid {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  flex: 1;
  min-height: 0;
  overflow: hidden;
}

.sectionsGrid>.sectionCard {
  flex: 1;
  min-height: 0;
}

.sectionCard {
  background-color: var(--panel-bg);
  border: 1px solid var(--panel-border);
  border-radius: var(--card-radius);
  padding: 1.25rem;
  box-shadow: 0 5px 18px rgba(3, 7, 18, 0.45);
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  min-height: 0;
}

.sectionCard[hidden] {
  display: none !important;
}

.sectionCard--summary {
  flex: 0 0 auto;
  max-height: clamp(180px, 32vh, 260px);
  display: flex;
  flex-direction: column;
}

.ghostButton {
  background: transparent;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 999px;
  color: var(--text-primary);
  padding: 0.45rem 1.25rem;
  cursor: pointer;
  font-size: 0.85rem;
  transition: border-color 0.2s ease, color 0.2s ease;
  width: fit-content;
}

.ghostButton:hover,
.ghostButton:focus-visible {
  border-color: var(--accent);
  color: var(--accent);
}

.cardHeader {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  align-items: flex-start;
  margin-bottom: 0.5rem;
}

.cardHeader h3 {
  margin: 0.25rem 0 0;
}

.cardHeader button {
  border: none;
  border-radius: 999px;
  padding: 0.65rem 1.5rem;
  background: var(--accent);
  color: #04111f;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s ease, transform 0.2s ease;
}

.cardHeader button:hover,
.cardHeader button:focus-visible {
  transform: translateY(-1px);
  background: #7fd6ff;
}

.cardHeader button:active {
  transform: translateY(0);
}

.newTaskForm,
.taskFilters,
.tasksList,
.editTaskForm,
.sectionCard--summary {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.02);
}
#section-list{
  padding: 0px
}
.newTaskForm {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 1rem;
  padding: 0.1rem;
}

.formField {
  display: flex;
  flex-direction: column;
  gap: 0.35rem;

}

#newTaskStatus {
  max-width: 200px;
}

.formField>label,
.formField>span {
  font-size: 0.85rem;
  font-weight: 600;
  color: var(--text-muted);
}

.newTaskForm input,
.newTaskForm textarea,
.newTaskForm select,
.taskFilters input,
.statusFilterChip,
.editTaskForm input,
.editTaskForm textarea,
.editTaskForm select,
.tasksList select {
  background: #000;
  border: 1px solid var(--panel-border);
  border-radius: 10px;
  color: var(--text-primary);
  padding: 0.65rem 0.85rem;
  font-size: 0.75rem;
}

.newTaskForm textarea,
.editTaskForm textarea {
  resize: vertical;
  min-height: 120px;
}

.markdownPreview {
  margin-top: 0.75rem;
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 10px;
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.03);
}

.markdownEditorHeader {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.markdownToggle {
  display: inline-flex;
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 999px;
  overflow: hidden;
}

.markdownToggle button {
  border: none;
  background: transparent;
  color: var(--text-muted);
  padding: 0.2rem 0.8rem;
  font-size: 0.75rem;
  cursor: pointer;
}

.markdownToggle button.is-active {
  background: rgba(94, 199, 244, 0.2);
  color: var(--text-primary);
}

.markdownToggle button:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

.markdownPreviewLabel {
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  color: var(--text-muted);
  display: block;
  margin-bottom: 0.35rem;
}

.markdownPreviewContent {
  color: var(--text-primary);
  min-height: 1.5rem;
}

.markdownPreviewContent:empty::before {
  content: "Nothing to preview yet.";
  color: var(--text-muted);
}

.newTaskForm button {
  align-self: center;
  width: fit-content;
  border: none;
  border-radius: 999px;
  padding: 0.85rem 1.75rem;
  font-weight: 600;
  cursor: pointer;
  background: #7c5dfa;
  color: #fff;
  height: fit-content;
  transition: background 0.2s ease, transform 0.2s ease;
}

.newTaskForm button:hover,
.newTaskForm button:focus-visible {
  background: #9c7dfc;
  transform: translateY(-1px);
}

.newTaskForm button:active {
  transform: translateY(0);
}

.taskFilters {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;

}

.createCardLayout {
  display: grid;
  gap: 1rem;
  grid-template-columns: 0.5fr 1fr;
}

.createCardPanel {
  background: rgba(0, 0, 0, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.06);
  border-radius: 12px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.createCardPanelHeader {
  display: flex;
  flex-direction: column;
  gap: 0.15rem;
}

.filterRow {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
  align-items: center;
}

.filterRow input {
  flex: 1;
  min-width: 200px;
}

.statusChips {
  display: flex;
  flex-wrap: wrap;
  gap: 0.1rem;
}

.statusFilterChip {
  border-radius: 999px;
  border: 1px solid transparent;
  cursor: pointer;
  transition: border-color 0.2s ease, background 0.2s ease;
  font-size: 0.85rem;
}

.statusFilterChip.is-active {
  border-color: var(--accent);
  background: rgba(94, 199, 244, 0.2);
  color: var(--accent);
}

.resetFilters {
  background: none;
  border: none;
  color: var(--text-muted);
  cursor: pointer;
  text-decoration: underline;
}

.filterActions {
  margin-left: auto;
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

.refreshFiltersButton {
  border-radius: 999px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: transparent;
  color: var(--text-primary);
  padding: 0.4rem 1.1rem;
  font-size: 0.85rem;
  cursor: pointer;
  transition: border-color 0.2s ease, color 0.2s ease;
}

.refreshFiltersButton:hover,
.refreshFiltersButton:focus-visible {
  border-color: var(--accent);
  color: var(--accent);
}

.cardScrollArea--filtersInline {
    max-height: fit-content;
    padding: 5px;
  }

.cardScrollArea--filtersInline .taskFilters {
  margin: 0;
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 12px;
  padding: 0.25rem;
}

.tasksList {
  list-style: none;
  margin: 0;
  padding: 0.75rem 1rem 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.tasksListEmpty {
  min-height: 160px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.25rem;
  color: var(--text-muted);
  text-align: center;
}

.taskCard {
  background: #050c1d;
  border: 1px solid transparent;
  border-radius: 14px;
  padding: 0.95rem;
  cursor: pointer;
  transition: border-color 0.2s ease, box-shadow 0.2s ease, background 0.2s ease;
  
}

.taskCard:hover,
.taskCard:focus-within {
  border-color: rgba(94, 199, 244, 0.35);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.25);
  background: rgba(5, 12, 29, 0.9);
}

.taskCard.is-selected {
  border-color: var(--accent);
  box-shadow: 0 0 0 2px rgba(94, 199, 244, 0.25);
}

.taskCardHeader {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  align-items: flex-start;
}

.taskCardHeader h4 {
  margin: 0.35rem 0 0;
}

.statusBadge {
  display: inline-flex;
  align-items: center;
  padding: 0.2rem 0.75rem;
  border-radius: 999px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin: 0;
}

.statusBadge--todo {
  background: rgba(94, 199, 244, 0.15);
  color: var(--accent);
}

.statusBadge--in_progress {
  background: rgba(255, 200, 87, 0.15);
  color: #f7a21b;
}

.statusBadge--blocked {
  background: rgba(255, 107, 107, 0.15);
  color: var(--danger);
}

.statusBadge--done {
  background: rgba(144, 238, 144, 0.15);
  color: #71f0b6;
}

.taskCardDescription {
  color: var(--text-muted);
  margin: 0.75rem 0;
  line-height: 1.5;
}

.taskCardDescription--rich :where(p, ul, ol, blockquote) {
  margin: 0.25rem 0;
}

.taskCardDescription--rich :where(ul, ol) {
  padding-left: 1.25rem;
}

.taskCardDescription--rich code {
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, monospace;
  background: rgba(255, 255, 255, 0.08);
  padding: 0.15rem 0.35rem;
  border-radius: 4px;
  color: #f7f7f7;
}

.taskCardDescription--rich a {
  color: var(--accent);
  text-decoration: underline;
  height: 100px;
  overflow: hidden ;
}
.taskCardDescription--rich {
  height: 50px;
  overflow: hidden ;
}

.taskCardDescription--rich :hover a {
  color: #7fd6ff;
}

.taskCardFooter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.taskCardFooter label {
  display: flex;
  flex-direction: column;
  gap: 0.35rem;
  font-size: 0.85rem;
  color: var(--text-muted);
}

.deleteTaskButton {
  border: none;
  background: rgba(167, 28, 28, 0.172);
  color: var(--text-primary);
  width: 32px;
  height: 32px;
  border-radius: 10px;
  font-size: 1.25rem;
  line-height: 1;
  cursor: pointer;
  transition: background 0.2s ease;
  margin: 0px;
  padding: 5px;
}

.deleteTaskButton:hover,
.deleteTaskButton:focus-visible {
  background: rgba(185, 81, 81, 0.605);
}

.mutedText {
  color: var(--text-muted);
  font-size: 0.85rem;
}

.editTaskForm {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  padding: 0;
  min-height: 0;
}

.editTaskForm button {
  align-self: flex-end;
  padding: 0.65rem 1.75rem;
  border-radius: 999px;
  border: none;
  cursor: pointer;
  background: var(--accent);
  color: #04111f;
  font-weight: 600;
  transition: background 0.2s ease, transform 0.2s ease;
}

.editTaskForm button:hover,
.editTaskForm button:focus-visible {
  background: #7fd6ff;
  transform: translateY(-1px);
}

.editTaskForm button:active {
  transform: translateY(0);
}

.sectionCard--summary button {
  background: #7c5dfa;
  color: #fff;
}

.sectionCard--summary button:disabled {
  opacity: 0.5;
}

.cardScrollArea {
  flex: 1;
  min-height: 0;
  overflow-y: auto;
  padding-right: 0.25rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.cardScrollArea::-webkit-scrollbar {
  width: 6px;
}

.cardScrollArea::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 999px;
}

.summaryScrollArea {
  max-height: 230px;
}

.summaryText {
  white-space: pre-wrap;
  line-height: 1.5;
}

.errorText {
  color: var(--danger);
}

.errorText.compact {
  margin: 0;
}

.hint {
  font-size: 0.85rem;
  color: var(--text-muted);
}

.hintInline {
  display: block;
  font-size: 0.75rem;
  color: var(--text-muted);
  font-weight: 400;
  margin-top: 0.15rem;
}

@media (max-width: 640px) {
  .todoApp {
    padding: calc(1rem + var(--safe-top)) calc(0.5rem + var(--safe-right)) calc(1rem + var(--safe-bottom)) calc(1rem + var(--safe-left));
  }

  .sectionCard {
    padding: 1rem;
  }

  .cardHeader button,
  .newTaskForm button,
  .editTaskForm button {
    width: fit-content;
  }

  .sectionNavButton {
    font-size: 0.9rem;
  }

  .taskFilters .filterRow {
    flex-direction: column;
    align-items: stretch;
  }

  .createCardPanel--form {
    width: 100%;
  }
  .createCardPanel--filters{
    display: none;
  }
  .createCardLayout {
    grid-template-columns: minmax(0, 1fr);
  }
  
}

@media (min-width: 768px) {
  .todoApp {
    padding: calc(0.5rem + var(--safe-top)) calc(0.5rem + var(--safe-right)) calc(0.5rem + var(--safe-bottom)) calc(0.5rem + var(--safe-left));
  }

  .sectionNavBar {
    top: calc(var(--safe-top) + 1rem);
  }

  .createCardLayout {
    grid-template-columns: minmax(260px, 0.5fr) minmax(320px, 1.1fr);
  }

  .cardScrollArea--filtersInline {
    max-height: fit-content;
  }
}

@media (min-width: 1024px) {
  .todoApp {
    max-width: 100%;
    gap: 1.75rem;
  }

  .sectionNavBar {
    display: none;
  }

  .sectionsGrid {
    display: grid;
    width: 100%;
    grid-template-columns: minmax(300px, 0.5fr) minmax(360px, 1.1fr);
    grid-template-rows: auto minmax(0, 1fr);
    grid-template-areas:
      "create create"
      "list details";
    gap: 1.25rem;
    align-items: stretch;
  }

  .sectionsGrid>.sectionCard[data-section="create"] {
    grid-area: create;
  }

  .sectionsGrid>.sectionCard[data-section="list"] {
    grid-area: list;
  }

  .sectionsGrid>.sectionCard[data-section="details"] {
    grid-area: details;
  }

  .sectionsGrid>.sectionCard[data-section="list"],
  .sectionsGrid>.sectionCard[data-section="details"] {
    min-height: 0;
    height: 100%;
  }

  .sectionCard[data-section="list"] .cardScrollArea,
  .sectionCard[data-section="details"] .cardScrollArea {
    min-height: 0;
  }

  .cardScrollArea--filtersInline {
    max-height: fit-content;
  }
}
